import { Component, OnInit } from '@angular/core';

import { DownloadService } from '../../angular-services/download.service';
import { Download } from '../../classes/download';

@Component({
  selector: 'app-list-downloads-admin',
  templateUrl: './list-downloads.component.html',
  styleUrls: ['./list-downloads.component.css']
})
export class ListDownloadsComponent implements OnInit {

  downloads: Download[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private downloadService: DownloadService) { }

  ngOnInit() {
    this.getDownloads();
  }

  getDownloads() {
    this.downloadService.getDownloads().subscribe(
      response => {
        if (response !== undefined) {
          this.downloads = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeDownload(download) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.downloads = this.downloads.filter(d => d !== download);
      this.downloadService.removeDownload(download._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Download excluído com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
